  height: auto;
  display: flex;
  flex-direction: column;
}

.lazyload,
.lazyloading {
  opacity: 0;
}

.lazyloaded {
  opacity: 1;
  transition: opacity 300ms;
}

@font-face {
  font-family: "Proxima Nova";
  src: url("../assets/fonts/ProximaNova-Light.woff2") format("woff2"), url("../assets/fonts/ProximaNova-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../assets/fonts/ProximaNova-Regular.woff2") format("woff2"), url("../assets/fonts/ProximaNova-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: url("../assets/fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../assets/fonts/ProximaNova-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

:root {
  --max-container-width: 158rem;
  --content-padding: 5rem;
  --primary-color: #f4e5a0;
  --biege: #f1dfd6;
  --dark-biege: #bb9b8c;
  --text-color: black;
  --proxima: "Proxima Nova", sans-serif;
  --cormorant: "Cormorant", serif;
  --fixed-bottom-panel-height: 11rem;
}

html {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

body {
  font-size: 1.6rem;
  line-height: 1.2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: var(--proxima);
  color: var(--text-color);
}

main {
  display: block;
}

ul,
ol {
  list-style: none;
}

ol {
  counter-reset: ordered-list;
}
ol li {
  counter-increment: ordered-list;
}
ol li::before {
  content: counter(ordered-list) ".";
}

input,
textarea {
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
  box-shadow: none;
  border-radius: 0;
  width: 100%;
  background-color: transparent;
  display: block;
}

input:not([type=checkbox]):not([type=radio]) {
  appearance: none;
  -webkit-appearance: none;
}

textarea {
  resize: none;
  box-shadow: none;
  display: block;
  width: 100%;
  overflow: auto;
  height: 20rem;
  scrollbar-width: none;
}
textarea::-webkit-scrollbar {
  background-color: white;
  width: 0.6rem;
}
textarea::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
}
textarea::-webkit-scrollbar-button {
  display: none;
}
textarea::-webkit-scrollbar-track {
  background-color: #f5f5f5;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

input[type=search] {
  -webkit-appearance: textfield;
}

input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  display: none;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
  -webkit-appearance: none;
  font: inherit;
  color: inherit;
  outline: none;
  display: inline-block;
  position: relative;
  transition: color 0.2s;
}

a {
  background-color: transparent;
  text-decoration: none;
  transition: color 0.2s;
}
a:link, a:visited {
  color: inherit;
}

svg {
  flex-shrink: 0;
  fill: currentColor;
  transition: fill 0.2s;
}

img {
  border-style: none;
  max-width: 100%;
  display: block;
}

img[data-sizes=auto] {
  display: block;
  width: 100%;
}

img.lazyload:not([src]) {
  visibility: hidden;
}

table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
}

th {
  font-weight: 400;
}

td,
th {
  text-align: left;
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--cormorant);
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.no-scroll {
  overflow-y: scroll;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.no-scroll body {
  height: auto;
  overflow: hidden;
}

::selection {
  color: white;
  background-color: var(--primary-color);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  background-color: transparent;
}

.container {
  width: 100%;
  max-width: calc(var(--max-container-width) + 2 * var(--content-padding));
  margin: 0 auto;
  padding: 0 var(--content-padding);
}
@supports (width: max(20vw, 100px)) {
  .container {
    padding-left: max(env(safe-area-inset-left, 1.4rem), var(--content-padding));
    padding-right: max(env(safe-area-inset-right, 1.4rem), var(--content-padding));
  }
}

.page-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.page-header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 250;
  pointer-events: none;
}
.mobile-menu-open .page-header {
  padding-right: var(--sb-width, 0);
}

.page-header__row {
  display: grid;
  grid-template-columns: minmax(0, 1fr) auto minmax(0, 1fr);
  grid-gap: 2rem;
  align-items: flex-start;
  padding-top: 6rem;
}

.page-header__logo {
  grid-column: 2/3;
  pointer-events: all;
}

.page-header__logo-image {
  width: 14rem;
  height: auto;
  display: block;
}

.page-header__controls {
  grid-column: 3/4;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.page-header__languages {
  display: flex;
  align-items: center;
  gap: 1.6rem;
  pointer-events: all;
}

.page-header__languages-link {
  font-size: 1.4rem;
  line-height: 110%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.4);
}
.page-header__languages-link:link, .page-header__languages-link:visited {
  color: rgba(255, 255, 255, 0.4);
}
.no-touch .page-header__languages-link:hover, .touch .page-header__languages-link:active {
  color: white;
}
.page-header__languages-link.active {
  color: white;
}
.mobile-menu-open .page-header__languages-link {
  color: rgba(0, 0, 0, 0.4);
}
.mobile-menu-open .page-header__languages-link:link, .mobile-menu-open .page-header__languages-link:visited {
  color: rgba(0, 0, 0, 0.4);
}
.no-touch .mobile-menu-open .page-header__languages-link:hover, .touch .mobile-menu-open .page-header__languages-link:active {
  color: black;
}
.mobile-menu-open .page-header__languages-link.active {
  color: black;
}

.page-header__burger {
  width: 8rem;
  height: 8rem;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  flex-shrink: 0;
  margin-left: 4.5rem;
  pointer-events: all;
}
.page-header__burger > svg {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  fill: rgba(244, 229, 160, 0.4);
}
.page-header__burger > span {
  width: 2.2rem;
  height: 0.1rem;
  background: #f4e5a0;
  margin-bottom: 0.7rem;
  transition: transform 0.3s, opacity 0.3s, background-color 0.3s;
}
.page-header__burger > span:last-of-type {
  margin-bottom: 0;
}
.mobile-menu-open .page-header__burger > span {
  background-color: black;
}

.page-footer {
  padding-top: 11rem;
  padding-bottom: 5.5rem;
  background: #f1dfd6;
}

.page-footer__top-row {
  padding-top: 11rem;
}

.page-footer__row {
  margin-bottom: 17rem;
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  grid-gap: 2rem;
}
.page-footer__row:last-child {
  margin-bottom: 0;
}

.page-footer__nav {
  grid-column: span 2;
}

.page-footer__nav-list {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-gap: 2rem;
  grid-row-gap: 2rem;
}

.page-footer__nav-link {
  font-size: 2.5rem;
  line-height: 110%;
}
.no-touch .page-footer__nav-link:hover, .touch .page-footer__nav-link:active {
  color: #bb9b8c;
}

.page-footer__secondary-nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.page-footer__secondary-nav-link {
  font-size: 2rem;
  line-height: 140%;
  margin-bottom: 1.2rem;
}
.page-footer__secondary-nav-link:last-child {
  margin-bottom: 0;
}
.no-touch .page-footer__secondary-nav-link:hover, .touch .page-footer__secondary-nav-link:active {
  color: #bb9b8c;
}

.page-footer__contacts {
  display: flex;
  flex-direction: column;
}

.page-footer__phone-link {
  font-size: 2.5rem;
  line-height: 110%;
  margin-bottom: 1.8rem;
}

.page-footer__mail-link {
  font-size: 2.5rem;
  line-height: 110%;
  text-decoration-line: underline;
  color: #bb9b8c;
  margin-bottom: 4.5rem;
}
.page-footer__mail-link:link, .page-footer__mail-link:visited {
  color: #bb9b8c;
}
.no-touch .page-footer__mail-link:hover, .touch .page-footer__mail-link:active {
  text-decoration: none;
}

.page-footer__address {
  font-size: 2.5rem;
  line-height: 110%;
}

.page-footer__copyright {
  font-size: 1.8rem;
  align-self: center;
  line-height: 120%;
}

.page-footer__social-list {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.page-footer__social-link {
  width: 2rem;
  height: 2rem;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
}
.page-footer__social-link svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: #bb9b8c;
}

.page-footer__made-by {
  grid-column: 4/5;
  align-self: center;
  display: inline-flex;
  align-items: center;
  font-size: 1.8rem;
  line-height: 120%;
}

.page-footer__made-by-logo {
  flex-shrink: 0;
  margin-left: 0.6rem;
  width: 10rem;
  height: auto;
  position: relative;
  bottom: -0.2rem;
}

.home-section {
  position: relative;
  color: white;
}

.home-section__bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
}
.home-section__bg img,
.home-section__bg video {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.home-section__bg::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, var(--overlay-opacity, 0.3));
}

.home-section__bg-parallax-layer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.home-section__content {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.home-section__row {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  margin-bottom: 3.2rem;
}
.home-section__row--centered {
  align-items: center;
}
.home-section__row:last-child {
  margin-bottom: 5rem;
}

.home-section__col {
  display: flex;
  flex-direction: column;
}

.home-section:nth-child(odd) .home-section__col:nth-child(2) {
  align-items: flex-end;
}

.home-section__heading {
  font-weight: 500;
  font-size: 6rem;
  line-height: 120%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.home-section__link {
  display: inline-flex;
  align-items: center;
  font-weight: 600;
  font-size: 1.6rem;
  line-height: 140%;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  color: #f4e5a0;
  margin-top: 3.5rem;
}
.home-section__link:link, .home-section__link:visited {
  color: #f4e5a0;
}

.home-section__link-icon {
  width: 6rem;
  height: 6rem;
  flex-shrink: 0;
  margin-right: 1.5rem;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.home-section__link-icon .icon-rombus-filled {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  fill: #f4e5a0;
  z-index: -1;
}
.home-section__link-icon .icon-arrow-right {
  width: 1rem;
  height: 1.8rem;
  fill: black;
}

.home-section__text {
  margin-top: 1.5rem;
  font-size: 1.8rem;
  line-height: 120%;
  max-width: 38rem;
}

.home-section__intro-heading {
  font-weight: 500;
  font-size: 4.3rem;
  line-height: 120%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  max-width: 70rem;
  align-self: stretch;
}

.home-section__events-slider {
  display: flex;
  align-items: flex-start;
}

.home-section__events-slider-content {
  width: 18.5rem;
}

.home-section__events-slider-heading {
  font-weight: 600;
  font-size: 1.6rem;
  line-height: 140%;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  margin-bottom: 1.4rem;
}

.home-section__events-slider-card-image-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  min-height: 12rem;
  position: relative;
  z-index: 1;
  padding: 0.6rem 0.9rem;
  justify-content: flex-end;
}
.home-section__events-slider-card-image-container::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
}

.home-section__events-slider-card-text {
  font-size: 1.5rem;
  line-height: 125%;
  letter-spacing: 0.02em;
  font-weight: 300;
}

.home-section__events-slider-card-image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.home-section__events-slider-card-row {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 30;
}

.home-section__events-slider-card-title {
  width: 50%;
  flex-grow: 1;
  font-weight: 600;
  font-size: 1.8rem;
  font-family: var(--proxima);
  line-height: 120%;
  letter-spacing: 0.18em;
  text-transform: uppercase;
}

.home-section__events-slider-card-icon {
  width: 3.8rem;
  height: 3.8rem;
  flex-shrink: 0;
  margin-left: 1.5rem;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.home-section__events-slider-card-icon .icon-rombus-filled {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  fill: rgba(255, 255, 255, 0.2);
}
.home-section__events-slider-card-icon .icon-fat-arrow-right {
  width: 1.5rem;
  height: 1.5rem;
  fill: #fff;
}

.home-section__events-slider-arrows {
  display: flex;
  align-items: center;
  margin-left: 1.8rem;
  margin-top: 7rem;
}

.home-section__events-slider-arrow {
  width: 6rem;
  height: 6rem;
  flex-shrink: 0;
  position: relative;
  z-index: 1;
  transition: opacity 0.2s;
}
.home-section__events-slider-arrow.swiper-button-disabled {
  opacity: 0.5;
  cursor: default;
}
.home-section__events-slider-arrow .icon-rombus {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  fill: rgba(244, 229, 160, 0.4);
}
.home-section__events-slider-arrow .icon-arrow-left,
.home-section__events-slider-arrow .icon-arrow-right {
  width: 1rem;
  height: 1.8rem;
  fill: #f4e5a0;
}
.home-section__events-slider-arrow:last-child {
  margin-left: -1.4rem;
}

.fixed-bottom-panel {
  position: relative;
}

.fixed-bottom-panel__row {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  height: var(--fixed-bottom-panel-height, 0);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.fixed-bottom-panel__col {
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
}
.fixed-bottom-panel__col:last-child {
  border-right: none;
}

.fixed-bottom-panel__ask-question {
  display: inline-flex;
  align-items: center;
  font-size: 1.2rem;
  line-height: 140%;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  color: #f4e5a0;
}
.fixed-bottom-panel__ask-question:link, .fixed-bottom-panel__ask-question:visited {
  color: #f4e5a0;
}

.fixed-bottom-panel__ask-question-icon {
  width: 6rem;
  height: 6rem;
  flex-shrink: 0;
  margin-right: 3rem;
  position: relative;
  z-index: 1;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.fixed-bottom-panel__ask-question-icon .icon-rombus {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  fill: rgba(244, 229, 160, 0.4);
}
.fixed-bottom-panel__ask-question-icon .icon-question {
  width: 1.8rem;
  height: 1.8rem;
  fill: #f4e5a0;
}

.fixed-bottom-panel__filters {
  width: 100%;
  align-self: stretch;
  position: relative;
  z-index: 250;
  display: flex;
  align-items: center;
  padding-left: 3.2rem;
}
.fixed-bottom-panel__filters::before {
  content: "";
  position: absolute;
  left: 0;
  top: -1px;
  right: calc((100vw - 100% - var(--sb-width)) / 2 * -1);
  bottom: -1px;
  background: #bb9b8c;
  transform-origin: center bottom;
  transform: scaleY(0);
  transition: transform 0.4s;
}
.mobile-menu-open .fixed-bottom-panel__filters::before {
  transform: scaleY(1);
  transition: transform 0.3s ease-out 0.7s;
}

.fixed-bottom-panel__filters-inputs {
  width: 50%;
  flex-grow: 1;
  display: flex;
  align-items: center;
}

.fixed-bottom-panel__filters-input-wrapper {
  margin-right: 3rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
}
.fixed-bottom-panel__filters-input-wrapper:last-child {
  margin-right: 0;
}

.fixed-bottom-panel__filters-submit {
  flex-shrink: 0;
  margin-left: 3rem;
}

.fixed-bottom-panel__filters-input-label {
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 140%;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 0.7rem;
  font-family: var(--cormorant);
}

.fixed-bottom-panel__filters-input {
  font-size: 1.2rem;
  line-height: 140%;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  color: white;
  padding-right: 3rem;
  background-image: url("../img/dropdown-arrow.svg");
  background-position: right center;
  background-repeat: no-repeat;
  background-size: 2rem 2rem;
  width: 11em;
}

.fixed-bottom-panel__filters-submit {
  display: inline-flex;
  align-items: center;
}

.fixed-bottom-panel__filters-submit-icon {
  flex-shrink: 0;
  z-index: 1;
  width: 6rem;
  height: 6rem;
  margin-right: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.fixed-bottom-panel__filters-submit-icon .icon-rombus {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  fill: rgba(244, 229, 160, 0.4);
}
.fixed-bottom-panel__filters-submit-icon .icon-search {
  fill: #f4e5a0;
  width: 1.8rem;
  height: 1.8rem;
  flex-shrink: 0;
}

.fixed-bottom-panel__filters-submit-text {
  font-size: 1.2rem;
  line-height: 140%;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  color: #f4e5a0;
}

.services {
  padding-top: 2rem;
  padding-bottom: 4.5rem;
}

.services__list {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-gap: 2rem;
}

.services__list-item--large {
  grid-column: 1/-1;
}

.services__card {
  position: relative;
  z-index: 1;
  color: white;
  min-height: 56rem;
  display: flex;
  flex-direction: column;
}
.services__card:link, .services__card:visited {
  color: white;
}
.no-touch .services__card:hover .services__card-bg-image, .touch .services__card:active .services__card-bg-image {
  transform: scale(1.03);
  transition: transform 1s ease-out;
}

.services__card-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: var(--dark-biege);
  overflow: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
}
.services__card-bg::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, var(--overlay-opacity, 0.3));
}

.services__card-bg-image {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.6s ease-in;
}

.services__card-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 4.5rem 5rem;
}

.services__card-row {
  display: flex;
  align-items: flex-end;
}

.services__card-heading {
  width: 50%;
  flex-grow: 1;
  font-weight: 600;
  font-size: 4rem;
  line-height: 120%;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.services__card-icon {
  flex-shrink: 0;
  margin-left: 3rem;
  width: 5.6rem;
  height: 5.6rem;
  position: relative;
  z-index: 1;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  bottom: -0.4rem;
}
.services__card-icon .icon-rombus-filled {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  fill: #f4e5a0;
  z-index: -1;
}
.services__card-icon .icon-arrow-right {
  width: 1rem;
  height: 1.8rem;
  fill: black;
}

.menu {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  z-index: 200;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(9, 22, 36, 0.4);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s, visibility 0s linear 0.4s;
}
.mobile-menu-open .menu {
  padding-right: var(--sb-width, 0);
}
.mobile-menu-open .menu {
  opacity: 1;
  visibility: inherit;
  transition: opacity 0.4s, visibility 0.4s;
}

.menu__inner {
  margin-left: auto;
  width: 50%;
  flex-grow: 1;
  background-color: #fff;
  padding-left: 5rem;
  padding-right: 5rem;
  padding-top: 6rem;
  color: black;
  padding-bottom: var(--fixed-bottom-panel-height);
  transition: transform 0.4s ease-in;
  transform: translateX(100%);
}
.mobile-menu-open .menu__inner {
  transform: translateX(0%);
  transition: transform 0.7s ease-out;
}

.menu__nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 6rem;
}

.menu__nav-link {
  margin-bottom: 2rem;
  font-size: 2.5rem;
  line-height: 110%;
}
.menu__nav-link:last-child {
  margin-bottom: 0;
}
.no-touch .menu__nav-link:hover, .touch .menu__nav-link:active {
  color: #bb9b8c;
}

.menu__secondary-nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.menu__secondary-nav-link {
  font-size: 2rem;
  line-height: 140%;
  margin-bottom: 1.2rem;
}
.menu__secondary-nav-link:last-child {
  margin-bottom: 0;
}
.no-touch .menu__secondary-nav-link:hover, .touch .menu__secondary-nav-link:active {
  color: #bb9b8c;
}

.menu__row {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 3rem;
}

.menu__social-list {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-left: 3rem;
  flex-shrink: 0;
}

.menu__social-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
}
.menu__social-link svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: #bb9b8c;
  transition: fill 0.2s;
}
.no-touch .menu__social-link:hover svg, .touch .menu__social-link:active svg {
  fill: black;
}
